import axios from 'axios'
export default {
  name: 'list',
  data () {
    return {
      userName: '',
      deleteUser: false,
      levelChoose: false,
      levelChange: Number,
      list: [],
      level: [
        {
          value: this.PI,
          label: 'PI'
        },
        {
          value: this.PR,
          label: 'PR'
        },
        {
          value: this.assistant,
          label: 'assistant'
        },
        {
          value: this.student,
          label: 'student'
        },
        {
          value: this.stranger,
          label: 'stranger'
        }
      ]
    }
  },
  methods: {
    getUserList () {
      axios.get(this.url + '/user/list')
        .then(this.getUserListSucc)
    },
    getUserListSucc (res) {
      if (res.status === 200) {
        const data = res.data
        this.list = data
      }
    },
    handleEdit (index, row) {
      this.levelChoose = true
      sessionStorage.setItem('uid', row.id)
      this.levelChange = row.level
      this.userName = row.name
    },
    handleDelete (index, row) {
      this.deleteUser = true
      this.userName = row.name
      sessionStorage.setItem('uid', row.id)
    },
    delUser () {
      this.$axios({
        method: 'delete',
        url: this.url + '/user/id/' + sessionStorage.getItem('uid')
      }).then((res) => {
        if (res.status === 200) {
          alert('删除成功！')
          axios.get(this.url + '/user/list')
            .then(this.getUserListSucc)
          this.deleteUser = false
        }
      }).catch((err) => {
        alert(err)
      })
    },
    updateLevel () {
      this.$axios({
        method: 'put',
        url: this.url + '/user/update',
        data: {
          id: parseInt(sessionStorage.getItem('uid')),
          level: this.levelChange
        }
      }).then((user) => {
        if (user.status === 200) {
          alert('修改成功！')
          axios.get(this.url + '/user/list')
            .then(this.getUserListSucc)
          this.levelChoose = false
        } else {
          alert(user.data)
        }
      }).catch((err) => {
        alert(err)
      })
    }
  },
  mounted () {
    this.getUserList()
  }
}



